<template>
    <div class="page page-details scrollTop">
        <div class="company padding bg">
            <header class="details-header">
                <div class="title">
                    <h3 class="wrap">{{details.title}}</h3>
                    <p v-if="details.isAuthor != 1" :class="['no-collect', {'collect': collectStatus == 1}]" @click="getCollect"></p>
                </div>
                <ul>
                    <li>{{details.requestType}}</li>
                    <li>{{details.industryType}}</li>
                </ul>
                <div class="indc">
                    <p class="check" v-if="details.checkStatus == 0">{{$t('details.approving')}}</p>
                    <p class="no-check" v-if="details.checkStatus == 2">{{$t('details.failed')}}</p>
                    <p class="no-effect" v-if="details.checkStatus == 1 && details.effectDay < 0">{{$t('details.failure')}}</p>
                    <p class="time" v-if="details.checkStatus == 1 && details.effectDay > -1 && details.effectDay <= 15">
                        <span>{{details.effectDay}}</span>{{$t('details.day')}}
                    </p>
                    <p class="time" v-if='details.checkStatus == 1 && details.effectDay > 15'>{{$t('publish.time')}}</p>
                    <p class="address">{{details.area}}</p>
                </div>
            </header>
            <div class="details-email">
                <div class="email-left">
                    <h4>{{$t('details.publishTemail')}}</h4>
                    <p>{{details.connector}}</p>
                </div>
                <p class="email-btn" v-if="details.isAuthor != 1" @click="getAuthor">{{$t('details.concat')}}</p>
            </div>
            <div class="detail-content">
                <div class="common" v-if="details.products">
                    <h3 class="title-before">{{$t('publish.product')}}</h3>
                    <p class="wrap">{{details.products}}</p>
                </div>
                <div class="common">
                    <h3 class="title-before">{{$t('details.details')}}</h3>
                    <div :class="['wrap', {'details-article': allShow}]">
                        <p class="article wrap">{{details.description}}</p>
                        <div class="details-img" v-if="!allShow">
                            <ul>
                                <li v-for="img in imgLists">
                                    <img :src="img.url" alt="">
                                </li>
                            </ul>
                        </div>
                        <p class="all" v-if="allShow" @click="seeAll"><span>...</span>{{$t('details.expandAll')}}</p>
                    </div>
                </div>
                <div class="details-company" :class='details.istrust == 1 ? "backgroundImg" : ""' @click="goApprove">
                    <div class="company-left" :style="'background-image: url(' + details.iconurl + ');'"></div>
                    <div class="company-right">
                        <div class="company-infos">
                            <p class="title wrap">{{details.company}}<span class="tag" v-if="details.istrust == 1">{{$t('base.trusted')}}</span></p>
                        </div>
                        <p class="web ellipsis" v-if="details.istrust == 1">{{details.companyUrl}}</p>
                        <p class="web ellipsis" v-if="details.istrust == 0">{{details.companyArea}}</p>
                        <p class="web ellipsis" v-if="details.istrust == 0">{{$t('publish.money')}}：{{details.registerFee}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="recommend-infos bg" v-if="recomList.length">
            <h3 class="title-before">{{$t('details.recommend')}}</h3>
            <ul>
                <li v-for="recom in recomList" @click="goDetails(recom)">
                    <h4 class="ellipsis">{{recom.title}}</h4>
                    <p class="art ellipsis">{{recom.description}}</p>
                    <div class="infos">
                        <p class="tag">{{recom.company}}</p>
                        <p class="address">{{recom.area}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';
    import config from '../../config.js';

    export default {
        name: 'page-details',
        data () {
            return {
                connector: '',
                details: {},
                imgLists: [],
                recomList: [],
                collectStatus: '',
                tags: [],
                userList: '',
                allShow: false,
                pShow: true
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.details'));
            this.getDetails();
        },
        watch: {
            '$route' (to, from) {
                if (this.$route.name === 'details') {
                    this.postId = to.query.postId;
                    if (from.name === 'details') {
                        this.getDetails();  // 配合keep-alive,路由变化重新加载...
                    }
                }
            }
        },
        updated () {
            if (this.pShow) {
                if (document.querySelector('.article').getClientRects().length > 10) {
                    this.allShow = true;
                } else {
                    this.allShow = false;
                }
            }
        },
        methods: {
            getDetails () {
                let self = this,
                    params = {};
                params.postId = self.$route.query.postId;
                params.userList = config.userList;
                publicPost(getApiUrl('getPostInfo'), params).then((res) => {
                    document.getElementsByClassName('scrollTop')[0].scrollTop = 0;
                    self.details = res;
                    self.collectStatus = res.isfavorite;
                    res.descrimgs ? self.imgLists = JSON.parse(res.descrimgs) : self.imgLists = [];
                    self.recomList = res.recomList;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            },
            getCollect () {
                let self = this,
                    params = {};
                params.postId = self.$route.query.postId;
                params.userList = config.userList;
                if (this.collectStatus === 1) {
                    params.suc = 0;
                } else {
                    params.suc = 1;
                }
                publicPost(getApiUrl('toFavorite'), params).then((res) => {
                    this.collectStatus === 1 ? this.collectStatus = 0 : this.collectStatus = 1;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            },
            getAuthor () {
                let self = this;
                window.toongine.protocol.request({
                    params: {
                        scheme: 'toon',
                        domain: 'ciftis',
                        path: '/getTemailList',
                        query: {}
                    },
                    callback: function (result) {
                        window.toongine.tcTemail.chat.createSession({
                            params: {
                                sessionType: '0',
                                myTmail: result.data.tmails,
                                talkerTmail: self.details.connector
                            },
                            callback: function (res) {

                            }
                        });
                    }
                });
            },
            seeAll () {
                this.pShow = false;
                this.allShow = false;
            },
            goApprove () {
                if (this.details.istrust === 1) {
                    this.$router.push({name: 'approve-details', query: {userId: this.details.userId}});
                }
            },
            goDetails (item) {
                this.$router.push({name: 'details', query: {postId: item.postId}});
            }
        }
    };
</script>

